import { createSlice } from '@reduxjs/toolkit';
const initialState = {
  draggedData: {
    member: [],
    totalItems: 0,
    nlsLabel: {},
    revisions: [],
    tableData: [],
  },
};

const droppedObjectSlice = createSlice({
  name: "droppedObject",
  initialState,
  reducers: {
    setDroppedObjectData: (state, action) => {
      if (action.payload.revisions || action.payload.tableData) {
        // Merge revisions and tableData into existing draggedData
        state.draggedData = {
          ...state.draggedData,
          ...action.payload, // Merge new data (revisions and tableData)
        };
      } else {
        // Set initial draggedData (e.g., from fetchObjectDetails)
        state.draggedData = {
          ...action.payload.draggedData,
        };
      }
    },
  },
});

export const { setDroppedObjectData } = droppedObjectSlice.actions;
export default droppedObjectSlice.reducer;
